.experience-container {
  padding: clamp(2rem, 4vw, 4rem) 0;
  background-color: #000000;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'Inter', sans-serif;
}

.experience-container h2 {
  font-size: clamp(2rem, 3vw, 3rem);
  color: #1d68f2;
  margin-bottom: clamp(3rem, 4vw, 4rem);
  text-align: center;
  font-weight: 700;
}

.experience-timeline {
  display: flex;
  flex-direction: column;
  gap: clamp(2rem, 3vw, 3rem);
  max-width: 900px;
  width: 100%;
  margin: 0 auto;
  padding: 0 clamp(1rem, 3vw, 2rem);
}

.experience-card {
  background: rgba(29, 104, 242, 0.1);
  border-radius: 8px;
  padding: clamp(1.5rem, 2vw, 2rem);
  border-left: 4px solid var(--card-accent, #1d68f2);
  transition: all 0.3s ease;
}

.experience-card:hover {
  transform: translateX(10px);
  background: rgba(29, 104, 242, 0.15);
  box-shadow: 0 4px 15px rgba(29, 104, 242, 0.2);
}

.experience-header {
  margin-bottom: clamp(1rem, 1.5vw, 1.5rem);
}

.experience-header h3 {
  font-size: clamp(1.4rem, 1.8vw, 1.8rem);
  color: var(--card-accent, #1d68f2);
  margin-bottom: 0.5rem;
  font-weight: 600;
}

.company-link {
  font-size: clamp(1.2rem, 1.4vw, 1.4rem);
  color: #ffffff;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  width: fit-content;
  transition: all 0.3s ease;
}

.company-link:hover {
  color: var(--card-accent, #1d68f2);
}

.external-link-icon {
  font-size: 0.8em;
  opacity: 0.8;
}

.experience-details {
  display: flex;
  gap: clamp(1.5rem, 2vw, 2rem);
  margin-bottom: clamp(1rem, 1.5vw, 1.5rem);
  color: #bbbbbb;
}

.detail {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: clamp(0.9rem, 1vw, 1rem);
}

.detail-icon {
  color: var(--card-accent, #1d68f2);
}

.experience-description ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.experience-description li {
  margin-bottom: 0.8rem;
  color: #ffffff;
  font-size: clamp(0.9rem, 1.1vw, 1.1rem);
  padding-left: 1.5rem;
  position: relative;
  line-height: 1.6;
}

.experience-description li::before {
  content: '•';
  color: var(--card-accent, #1d68f2);
  position: absolute;
  left: 0;
  font-weight: bold;
}

.experience-skills {
  display: flex;
  flex-wrap: wrap;
  gap: 0.8rem;
  margin-top: clamp(1.5rem, 2vw, 2rem);
}

.skill-tag {
  background: rgba(29, 104, 242, 0.2);
  color: #ffffff;
  padding: 0.4rem 1rem;
  border-radius: 20px;
  font-size: clamp(0.8rem, 0.9vw, 0.9rem);
  transition: all 0.3s ease;
  font-weight: 500;
}

.skill-tag:hover {
  background: var(--card-accent, #1d68f2);
  transform: translateY(-2px);
}

@media (max-width: 768px) {
  .experience-card {
    padding: 1.2rem;
  }

  .experience-details {
    flex-direction: column;
    gap: 0.8rem;
  }

  .experience-skills {
    gap: 0.6rem;
  }
}
